* {
 box-sizing: border-box;
}

body {
 margin: 0;
 padding: 0;

 background-color: #303030;
 height: 100%;
}

html,
body,
#root,
.container {
 height: 100%;
 display: flex;
 justify-content: center;
}

.loader {
 width: 100%;
 height: 100%;
 display: flex;
 justify-content: center;
 align-items: center;
 color: #f2f2f2;
}


.movies {
 display: flex;
 justify-content: space-between;
 align-items: flex-start;
 flex-wrap: wrap;
 padding: 50px;
 padding-top: 70px;
 width: 80%;
}

.summary_detail{
 color:#f2f2f2;
}
.movies .movie {
 width: 45%;
 background-color: #424242;
 margin-bottom: 70px;

 align-items: flex-start;
 justify-content: space-between;
 font-weight: 300;
 padding: 20px;
 border-radius: 5px;
 color: #adaeb9;
 box-shadow: 0 -2px 10px rgba(0,0,0,1);
}

.movie img {
 position: relative;
 top: -50px;
 max-width: 120px;
 margin-right: 30px;
 box-shadow: 0 -2px 10px rgba(0,0,0,1);
}

.movie .movie_title,
.movie .movie_year {
 margin: 0;
 font-weight: 300;
}

.movie .movie_title {
 margin-bottom: 5px;
 font-size: 24px;
 color: #f2f2f2;
}

.movie .movie_genres {
 list-style: none;
 padding: 0;
 margin: 0;
 display: flex;
 margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year {
 margin-right: 10px;
 font-size: 14px;
}